import React,{useState} from 'react';
import uuid from 'react-uuid'
import Dom from "./dom.mp3"
import Dak from "./dak.mp3"


function App() {
  const [bars, setBars] = useState([{
    id: uuid()
  }])
  //  doc.childNodes
  let interval = null;
  let count = -1;
  let countChild = 0;
function loopThrough() {
  count = -1;
  countChild = 0;
  if(interval == null)
  interval = setInterval(countBox, 1000);
  else{
    clearInterval(interval)
    interval = null
  }
      
}
 function countBox() {
  var doc = document.querySelector(".chords");
  var soundDom = document.querySelector("#domSound")
  var soundDak = document.querySelector("#dakSound")
  // var soundDak = document.querySelector("#dakSound")
  if(count < 3){count++}else{count = 0; countChild++};
  if(count === 3 && countChild === doc.childNodes.length -1){
  clearInterval(interval);
  interval = null
};
if(doc.childNodes[countChild].childNodes[count].classList[0] !== "change-color"){
  soundDom.currentTime = 0;
  soundDom.play()
  console.log(doc.childNodes[countChild].childNodes[count].classList)
  doc.childNodes[countChild].childNodes[count].classList.add("change-color")
}else{
  soundDak.currentTime = 0;
  soundDak.play();
  doc.childNodes[countChild].childNodes[count].classList.remove("change-color")
  console.log("dak")
}
  
 }
 const addBars = () => setBars([...bars,  { id: uuid() }]);

 const disableBar = (items) => { 
   setBars(bars.filter((item) => item.id !== items.id))
  
 }
  return (
    <div className="App">
      <h1>24-drum-festival</h1> 
      <div className="chords">
        {bars.map((item) => {
          return(
          <div className="add-chord" key  ={item.id}>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <button onClick={() => disableBar(item)}>x</button>
          </div>
          )
        })}
      </div>
      <div className="d-flex">
        <button onClick={loopThrough}>Play</button>
        <button onClick={addBars}>Add bar</button>
      </div>
      <audio id="domSound" src={Dom}></audio>
      <audio id="dakSound" src={Dak}></audio>
    </div>
  );
}

export default App;
